$font-family: "Avenir Next LT Pro", sans-serif
$second-family: "Gilroy", sans-serif
html
    position: relative
    overflow-x: hidden
body
    font-family: $font-family
    position: relative
    background: #0c131d
    background-image: url('../img/star_bg.svg')
    background-position: center
    background-repeat: repeat
    background-size: contain
    min-height: 100vh
    overflow: hidden
.container
    max-width: 1110px
    padding: 0 20px
    margin: 0 auto
    box-sizing: content-box
.btn
    &__blue
        border: 1.50px solid rgba(255, 255, 255, 0.1)
        border-radius: 50px
        backdrop-filter: blur(20px)
        background: linear-gradient( rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%)
        padding: 20px 30px
        font: 500 20px $font-family
        text-align: center
        color: #fff
        width: max-content
        z-index: 2
        border: none
        box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 1px 0 0 1px rgba(255, 255, 255, 0.3)
    &__wht
        border-radius: 50px
        padding: 20px 30px
        border: none
        background: linear-gradient(90deg, #87aedb 0%, #c9e1ff 100%)
        font: 500 20px $font-family
        color: #000
        width: max-content
        z-index: 2
section
    padding: 80px 0 0
    z-index: 1
    position: relative

.nav
    padding: 40px 0
    &__hum
        display: none
    .container
        display: flex
        justify-content: flex-start
        align-items: center
        max-width: 1491px
    &__logo
        max-width: 144px
        display: block
        img
            width: 100%
    &__list
        border: 1.50px solid rgba(255, 255, 255, 0.1)
        border-radius: 50px
        padding: 14px 25px
        backdrop-filter: blur(20px)
        background: linear-gradient(17deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0.05) 50.5%, rgba(255, 255, 255, 0.05) 100%)
        margin: 0 auto
        gap: 25px
        display: flex
        justify-content: center
        align-items: center
        a
            font: 400 16px $font-family
            letter-spacing: -0.05em
            text-align: center
            color: rgba(255, 255, 255, 0.6)
            transition: all .3s
            display: block
            &.active
                font-weight: 600
                color: #fff
            &:hover
                color: #fff
            &.droplist
                display: grid
                grid-template-columns: auto 9px
                gap: 5px
                align-items: center
                &::after
                    content: ''
                    width: 9px
                    height: 9px
                    background: url('../img/droplist_arrow.svg') center no-repeat
                    background-size: contain
    &__enter
        border-radius: 50px
        padding: 18px 28px
        border: none
        background: linear-gradient(90deg, #87aedb 0%, #c9e1ff 100%)
        font: 500 16px $font-family
        color: #000
        display: grid
        grid-template-columns: auto 10px
        gap: 8px
        align-items: center
        &::after
            content: ''
            display: block
            width: 10px
            height: 10px
            background: url('../img/link_arrow.svg') center no-repeat
            background-size: contain

.header
    padding: 130px 0 
    position: relative
    z-index: 1
    &__text
        font: 600 15px $font-family
        letter-spacing: 0.25em
        text-transform: uppercase
        text-align: center
        color: #fff
        mix-blend-mode: plus-lighter
        opacity: 0.4
    h1
        margin: 25px auto 0
        font: 300 64px $font-family
        text-align: center
        color: #fff
        strong
            font-weight: 700
        span
            font-weight: 600
            text-align: center
            background: linear-gradient(90deg, #87aedb 0%, #c9e1ff 100%)
            background-clip: text
            -webkit-background-clip: text
            -webkit-text-fill-color: transparent
    &__subtitle
        font: 400 20px $font-family
        text-align: center
        color: #fff
        opacity: 0.6
        max-width: 411px
        margin: 38px auto 0
    &__row
        margin: 60px auto 0
        display: flex
        justify-content: center
        align-items: center
        gap: 10px
    &::before
        content: ''
        display: block
        width: 100vw
        height: 1052px
        top: 0px
        left: 50%
        transform: translateX(-50%)
        position: absolute
        z-index: -1
        background: url('../img/bg_ellipse.svg') center no-repeat
        filter: blur(400px)

.run-line
    position: relative
    overflow: hidden
    z-index: 1
    &__wrapper
        display: flex
        justify-content: flex-start
        align-items: center
        gap: 10px
        padding: 2px
    &__row
        &+.run-line
                &__row
                    transform: scale(-1)
                    margin: 10px 0 0
                    .run-line__item
                        transform: scale(-1)
    &__item
        box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 1px 0 0 1px rgba(255, 255, 255, 0.3)
        border-radius: 20px
        background: linear-gradient(7deg, rgba(255, 255, 255, 0.02) 0%, rgba(255, 255, 255, 0.02) 100%)
        padding: 17px
        opacity: .5
        img
            width: 46px
            height: 46px
            object-fit: contain

.about
    padding: 18px 0 0
    h2
        font: 600 40px $font-family
        background: linear-gradient(90deg, #87aedb 0%, #c9e1ff 100%)
        background-clip: text
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        margin: 20px 0 35px
    h3
        font: 600 16px $font-family
        letter-spacing: 0.25em
        text-transform: uppercase
        color: #fff
    p
        opacity: .6
        font: 400 20px $font-family
        color: #fff
    &__btn
        margin: 50px 0 0
        display: flex
        justify-content: center
        align-items: center
        width: max-content
        gap: 10px
        .btn
            &__blue
                display: grid
                grid-template-columns: auto 16px
                gap: 11px
                align-items: center
                &::after
                    content: ''
                    display: block
                    width: 16px
                    height: 16px
                    background: url('../img/arrow.svg') center no-repeat
                    background-size: contain
    &__title
        position: relative
    &__wrapper
        position: relative
        display: grid
        grid-template-columns: 1fr 488px
        gap: 18px
        align-items: center
        &::before
            content: ''
            display: block
            width: 1484px
            max-width: 100vw
            bottom: 0
            position: absolute
            left: 50%
            transform: translateX(-50%)
            height: 2px
            background: #104774
            opacity: .2
        img
            width: 100%
    &__text
        padding: 62px 0 0
        display: grid
        grid-template-columns: 1fr 1fr
        gap: 30px
    &::before
        content: ''
        display: block
        width: 100vw
        height: 1052px
        top: 0px
        left: 100%
        transform: translateX(-50%)
        position: absolute
        z-index: -1
        background: url('../img/bg_ellipse.svg') center no-repeat
        filter: blur(400px)

.title
    p
        font: 600 16px $font-family
        letter-spacing: 0.25em
        text-transform: uppercase
        text-align: center
        color: #87aedb
    h2
        margin: 10px 0 0
        font: 600 40px $font-family
        text-align: center
        color: #fff

.prof
    &__wrapper
        margin: 40px 0 0
        display: grid
        grid-template-columns: 100%
        gap: 10px
    &__row
        display: grid
        gap: 10px
        &_therd
            grid-template-columns: 1fr 1fr 1fr
        &_two
            grid-template-columns: 1fr 1fr
    &__item
        position: relative
        background-position: center right
        background-size: contain
        background-repeat: no-repeat
        // box-shadow: 0 4px 30px 0 rgba(45, 102, 93, 0.3)
        box-shadow: -1px 0 0 1px rgba(255, 255, 255, 0.3) , 0 0 0 2px rgba(255, 255, 255, 0.1)
        border-radius: 20px
        height: 206px
        padding: 30px 25px
        display: flex
        flex-direction: column
        justify-content: space-between
        img
            width: 90px
        h3
            font: 700 28px $font-family
            color: #fff
        p
            font: 500 14px $font-family
            letter-spacing: 0.1em
            text-transform: uppercase
            color: #fff

.static
    &__subtitle
        margin: 30px auto 0
        max-width: 830px
        opacity: .6
        font: 300 20px $font-family
        text-align: center
        color: #fff
    &__item
        display: flex
        justify-content: space-between
        align-items: center
        gap: 20px
    &__wrapper
        display: flex
        flex-wrap: wrap
        gap: 50px
        justify-content: center
        align-items: center
        margin: 50px 0 0
        row-gap: 80px
    &__info
        p
            font: 600 16px $font-family
            text-transform: uppercase
            color: #fff
        h3
            font: 600 60px $font-family
            background: linear-gradient(90deg, #87aedb 0%, #c9e1ff 100%)
            background-clip: text
            -webkit-background-clip: text
            -webkit-text-fill-color: transparent
    &__graf
        width: 135px
        img
            width: 100%

.model
    &__row
        margin: 40px 0 0
        display: grid
        grid-template-columns: 1fr 1fr
        gap: 10px
        &_item
            display: grid
            grid-template-columns: 1fr 215px
            gap: 20px
            align-items: center
        &_title
            position: relative
            padding: 0 0 15px
            &::before
                content: ''
                display: block
                width: 100%
                height: 1px
                position: absolute
                bottom: 0
                left: 0
                background: linear-gradient(55deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0.0) 100%)
        &_img
            width: 100%
    &__title
        h4
            font: 700 12px $font-family
            letter-spacing: 0.25em
            text-transform: uppercase
            color: #87aedb
        h3
            font: 600 36px $font-family
            color: #fff
            margin: 0px 0 0
        p
            font: 400 16px $font-family
            color: #fff
            opacity: 0.6
            margin: 15px 0 0
    &__item
        position: relative
        padding: 35px 20px
        border-radius: 20px
        backdrop-filter: blur(20px)
        background: linear-gradient(55deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0.05) 100%)
        box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 1px 0 0 1px rgba(255, 255, 255, 0.3)
        z-index: 2
        &_wht
            background: linear-gradient(72deg, #87aedb 0%, #c9e1ff 100%)
            box-shadow: none
            h3
                color: #000
            h4
                color: #fff
    &__per
        font: 600 16px $font-family
        color: rgba(255, 255, 255, 0.92)
        margin: 15px 0 0
        &_wrapper
            margin: 8px 0 0
            display: grid
            grid-template-columns: auto 1fr
            gap: 6px
            align-items: center
            .model__text
                margin: 0
        &_text
            font: 600 40px $font-family
            background: linear-gradient(90deg, #87aedb 0%, #c9e1ff 100%)
            background-clip: text
            -webkit-background-clip: text
            -webkit-text-fill-color: transparent
    &__text
        margin: 33px 0 0
        p
            font: 600 16px $font-family
            color: rgba(255, 255, 255, 0.92)
            &+p
                font-weight: 400
    &__wrapper
        margin: 10px 0 0
        display: grid
        grid-template-columns: 1fr 1fr 1fr
        gap: 10px
        &_item
            display: flex
            flex-direction: column
            justify-content: space-between
        &_img
            margin: 20px -20px
            width: calc( 100% + 40px )
    &__link
        padding: 24px 0 0
        display: flex
        justify-content: space-between
        align-items: center
        position: relative
        font: 500 20px $font-family
        color: #fff
        &::before
            content: ''
            display: block
            width: 100%
            height: 1px
            position: absolute
            top: 0
            left: 0
            background: linear-gradient(55deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0.0) 100%)
        &::after
            content: ''
            display: block
            width: 16px
            height: 16px
            background: url('../img/arrow.svg') center no-repeat
    &__num
        position: absolute
        top: 30px
        right: 30px
        background: rgba(0, 0, 0, 0.05)
        border-radius: 10px
        width: max-content
        padding: 8px
        p
            font: 600 12px $font-family
            letter-spacing: 0.15em
            text-transform: uppercase
            color: rgba(0, 0, 0, 0.6)
            span
                font: 700 16px $font-family
                color: #000
                display: block
                margin: 3px 0 0
        &_text
            font: 400 16px $font-family
            color: #000
            opacity: .6
            margin: 20px 0
            max-width: 170px
        &_img
            position: absolute
            right: 0
            width: 242px
            bottom: 50px
            z-index: -1

.make
    &__wrapper
        margin: 40px 0 0
        height: 610px
        position: relative
    &__investor
        width: 170px
        position: absolute
        top: 70px
        left: 0
    &__subacc
        width: 170px
        position: absolute
        top: 28px
        left: 260px
        .make__img
            margin: 50px auto 0
    &__num
        border-radius: 100%
        width: 28px
        height: 28px
        display: flex
        justify-content: center
        align-items: center
        border: 1px solid rgba(255, 255, 255, 0.1)
        font: 700 10px $font-family
        text-align: center
        color: #fff
        margin: 0 auto
    &__name
        font: 700 14px $font-family
        text-align: center
        color: #fff
        margin: 8px 0 0
    &__text
        margin: 13px 0 0
        opacity: .6
        font: 400 12px $font-family
        text-align: center
        color: #fff
    &__img
        width: 120px
        height: 120px
        border-radius: 100%
        display: flex
        justify-content: center
        align-items: center
        border: 1px solid rgba(255, 255, 255, 0.1)
        margin: 35px auto 0
        img
            width: 68px
    &__traders
        top: 0
        left: 630px
    &__step
        position: absolute
        .make
            &__img
                width: 70px
                height: 70px
                border: none
                background: url('../img/border.svg') center no-repeat
                background-size: contain
                margin: 15px 0 0
                img
                    width: 36px
            &__text
                position: absolute
                top: 0
                left: calc( 100% + 48px )
                width: 160px
                text-align: left
            &__name
                margin: 0
                text-align: left
        &_row
            display: grid
            grid-template-columns: 28px 1fr
            gap: 6px
            align-items: center
    &__nft
        top: 134px
        left: 540px
    &__min
        left: 540px
        top: 288px
    &__ico
        left: 630px
        bottom: 77px
    &__main
        right: 0
        display: grid
        grid-template-columns: 144px 1fr
        gap: 35px
        align-items: center
        position: absolute
        top: 206px
        .make
            &__text,
            &__name
                text-align: left
            &__num
                margin: 0
            &__img
                width: 144px
                height: 144px
                margin: 0
                img
                    width: 70px
        &_row
            max-width: 159px
    &__profit
        position: absolute
        bottom: 0
        width: 100%
        &_img
            width: 837px
            margin: 0 auto
            position: relative
        &_arrow
            width: 100%
        &_text
            position: absolute
            left: 0
            width: 236px
            bottom: 38px
            font: 400 12px $font-family
            color: #fff
            opacity: .6
        &_coin
            width: 20px
            height: 20px
            position: absolute
            right: 91%
            top: 37%
            animation: moveAlongPath 5s linear infinite
            &:nth-child(1)
                animation-delay: 0s
            &:nth-child(2)
                animation-delay: 1s
            &:nth-child(3)
                animation-delay: 2s
            &:nth-child(4)
                animation-delay: 3s
            &:nth-child(5)
                animation-delay: 4s
        &_info
            position: absolute
            bottom: 30px
            left: 379px
            span
                display: block
                width: 8px
                height: 8px
                border-radius: 100%
                background: #fff
                margin: 35px auto 0
        .make
            &__num
                border-color: #87aedb
                color: #87aedb
    &__point
        position: absolute
        p
            font: 500 12px $font-family
            text-align: center
            color: #fff
            opacity: .6
        span
            display: block
            width: 8px
            height: 8px
            border-radius: 100%
            background: #87aedb
            margin: 20px auto 0
        &_1
            top: 124px
            right: 310px
        &_2
            top: 215px
            right: 395px
        &_3
            right: 395px
            top: 328px
        &_4
            top: 411px
            right: 310px
    &__invest
        position: relative
        width: 78px
        position: absolute
        left: 181px
        top: 275px
        &_arrow
            width: 100%
        &_coin
            position: absolute
            top: 50%
            left: 15px
            transform: translate(-50%,-50%) scale(0)
            width: 20px
            height: 20px
            opacity: 0
            animation: stepsWallet 2s infinite ease-in-out
            &:nth-child(2)
                animation-delay: .3s
            &:nth-child(3)
                animation-delay: .6s

.advant
    &__wrapper
        display: grid
        grid-template-columns: 1fr 1fr 1fr
        gap: 10px
        margin: 40px 0 0
    &__num
        font: 700 14px $font-family
        letter-spacing: 0.25em
        text-transform: uppercase
        color: #fff
    &__row
        display: grid
        grid-template-columns: 55px 1fr
        gap: 13px
        align-items: center
        margin: 15px 0 0
    &__img
        border-radius: 10px
        width: 55px
        height: 55px
        background: linear-gradient(133deg, #c9e1ff 0%, #87aedb 100%)
        display: flex
        justify-content: center
        align-items: center
        img
            width: 36px
    &__name
        font: 600 24px $font-family
        color: #fff
    &__text
        margin: 25px 0 0
        font: 400 16px $font-family
        color: #fff
        opacity: .6
    &__item
        border: 1.50px solid rgba(255, 255, 255, 0.1)
        border-radius: 20px
        min-height: 310px
        backdrop-filter: blur(20px)
        background: rgba(255, 255, 255, 0.05)
        padding: 35px 25px 25px
        display: flex
        flex-direction: column
        &_wht
            backdrop-filter: blur(20px)
            background: linear-gradient(327deg, #87aedb 0%, #c9e1ff 100%)
            .advant
                &__num,
                &__name
                    color: #0d1b29
                &__img
                    background: linear-gradient(315deg, #0d1b29 0%, rgba(13, 27, 41, 0.8) 100%)
                &__text
                    color: #0d1b29
    &__link
        margin: auto 0 0
        padding: 24px 0 0
        position: relative
        font: 500 20px $font-family
        color: #0d1b29
        display: flex
        justify-content: space-between
        align-items: center
        &::after
            width: 16px
            height: 16px
            content: ''
            display: block
            background: url('../img/arrow.svg') center no-repeat
            filter: invert(100%)
        &::before
            content: ''
            display: block
            width: 100%
            height: 1px
            position: absolute
            left: 0
            top: 0
            background: linear-gradient(55deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%)

.start
    &__wrapper
        margin: 80px 0 0
        display: grid
        grid-template-columns: 1fr 1fr 
        gap: 70px
    &__row
        display: grid
        grid-template-columns: 32px 1fr auto
        gap: 23px
        align-items: start
    &__logo
        width: 100%
    &__name
        p
            font: 700 12px $font-family
            letter-spacing: 0.25em
            text-transform: uppercase
            color: #87aedb
        h3
            font: 600 22px $font-family
            text-transform: uppercase
            color: #fff
    &__link
        font: 500 16px $font-family
        text-transform: uppercase
        text-align: right
        color: #87aedb
        display: grid
        grid-template-columns: 1fr 16px
        gap: 13px
        align-items: center
        &::after
            content: ''
            display: block
            width: 16px
            height: 16px
            background: url('../img/arrow_blue.svg') center no-repeat
            background-size: contain
    &__text
        margin: 25px 0 0
        font: 400 16px $font-family
        color: #fff
        opacity: .6

@media ( max-width: 1024px )
    .nav
        &__list,
        &__enter
            display: none
        &__hum
            display: flex
            justify-content: space-between
            flex-direction: column
            width: 30px
            height: 20px
            margin: 0 0 0 auto
            cursor: pointer
            position: relative
            span,
            &::before,
            &::after
                content: ''
                display: block
                width: 100%
                height: 1px
                position: relative
                // transition: all .3s
                background: #fff
        &.active
            position: fixed
            top: 0
            left: 0
            width: 100vw
            height: 100vh
            background: #0c131d
            z-index: 5
            .container
                flex-wrap: wrap
                height: 100%
                align-content: space-between
            .nav
                &__hum
                    span
                        display: none
                    &::before,
                    &::after
                        position: absolute
                        top: 50%
                        left: 50%
                        transform: translate(-50%, -50%) rotate(45deg)
                    &::after
                        transform: translate(-50%, -50%) rotate(-45deg)
                &__list
                    order: 2
                    width: 100%
                    display: grid
                    margin: 20px 0 0
                &__enter
                    display: grid
                    order: 3
                    width: 100%
                    margin: 20px 0 0
    .header
        padding: 80px 0
        h1
            font-size: 40px
    .about
        padding-top: 40px
        &__btn
            grid-template-columns: 100%
            display: grid
            button
                width: 100%
        &__wrapper
            grid-template-columns: 100%
            gap: 0
            img
                max-width: 500px
                margin: 0 0 0 auto
        &__text
            grid-template-columns: 100%
    .prof
        &__row
            grid-template-columns: 100%
    .title
        h2
            font-size: 32px
    .model
        &__row
            grid-template-columns: 100%
        &__wrapper
            grid-template-columns: 1fr 1fr
        &__item
            &_wht
                grid-column: 1/3
                order: 2
    .make
        &__wrapper
            height: auto
        &__invest
            display: none
        &__profit
            position: relative
            inset: 0
            display: flex
            flex-direction: column
            margin: 20px 0 0
            &_img
                display: none
            &_text
                position: relative
                inset: 0
                order: 2
                text-align: center
                margin: 10px auto
            &_info
                position: relative
                inset: 0
        &__investor
            position: relative
            inset: 0
            margin: 0 auto
            width: 100%
        &__subacc
            position: relative
            inset: 0
            margin: 20px auto 0
            width: 100%
            .make__img
                margin: 10px auto 0
        &__step
            position: relative
            inset: 0
            &_row
                width: max-content
                margin: 0 auto
            &_wrapper
                margin: 20px 0 0
                display: grid
                grid-template-columns: 100%
                gap: 20px
                justify-items: center
            .make
