@keyframes stepsWallet 
    0% 
        left: 15px
        opacity: 0
        transform: translate(-50%, -50%) scale(0)
    50% 
        opacity: 1
        transform: translate(-50%, -50%) scale(1)
    to
        left: calc(100% - 15px)
        opacity: 0
        transform: translate(-50%, -50%) scale(0)
@keyframes stepsArrow1 
    0% 
        width: 0
        height: 0
        transform: rotate(-25deg)
    50% 
        opacity: 1
    to 
        width: 100%
        height: 100%
        opacity: 0
        transform: rotate(0)
@keyframes stepsArrow2 
    0% 
        width: 0
        height: 0
